.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  font-size: 14px;
  background: linear-gradient(180deg, #d8b05c 0%, #fbfbfb 100%);
  .title {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 30px;
  }
  .btnWrap {
    display: flex;
    align-items: center;
    justify-content: center;
    .btn {
      background-color: #d9af5c;
      width: 200px;
    }
    .btn:hover {
      background-color: #d9af5c;
    }
  }
}
@keyframes flash {
  0%{opacity: 0;}
  100%{opacity: 1;}
}
.starHeader{
  height: 200px;
  span{
    width: 15px;
    height: 15px;
    background: url("../public/star.png") no-repeat;
    position: absolute;
    background-size:100% 100%;
    animation: flash 1s alternate infinite;
  }
  span:hover{
    transform: scale(3, 3) rotate(180deg) !important;
    transition: all 1s;
  }
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
